@import "../../styles/base/variables.scss";
@import "../../styles/base/responsiveMixin.scss";

.textWhite {
  color: white;
}

.Join {
  display: flex;
  justify-content: center;
  text-align: center;
  height: 100vh;
  align-items: center;
  background-color: #1a1a1d;

  .joinInnerContainer {
    width: 20%;

    .heading {
      color: white;
      font-size: 2.5em;
      padding-bottom: 10px;
      border-bottom: 2px solid white;
    }

    .joinInput {
      border-radius: 0;
      padding: 15px 20px;
      width: 100%;
    }

    .button {
      color: #fff !important;
      text-transform: uppercase;
      text-decoration: none;
      background: #2979ff;
      padding: 20px;
      border-radius: 5px;
      display: inline-block;
      border: none;
      width: 100%;
    }

    .mt-20 {
      margin-top: 20px;
    }
  }
}
@include atMedium {
  .Join {
    // height: 100%;
    .joinInnerContainer {
      width: 90%;
    }
  }
}
