@import "../../styles/base/variables.scss";
@import "../../styles/base/responsiveMixin.scss";

.InfoBar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #2979ff;
  border-radius: 4px 4px 0 0;
  height: 60px;
  width: 100%;

  .leftInnerContainer {
    flex: 0.5;
    display: flex;
    align-items: center;
    margin-left: 5%;
    color: white;
  }

  .rightInnerContainer {
    display: flex;
    flex: 0.5;
    justify-content: flex-end;
    margin-right: 5%;
    i {
      color: white;
    }
  }

  .onlineIcon {
    margin-right: 5%;
  }

  @include atSmall {
  }
}
